:root{
    --square-side-length: 50px
}

.crossword {
    
}
.actions-menu{
    display:grid;
    grid-template-columns:repeat(3,1fr);
    gap: 10px;
}
.board-and-clues {
    display:grid;
    grid-template-columns: repeat(2,1fr);
}
.square {
    width: var(--square-side-length);
    height: var(--square-side-length);
    border: 1px solid black;
    color: black;
    text-align: center;
    font-size: large;
}
.square-black {
    background-color: black;
}
.square-user {
    background-color:yellow;
}
.board {
  display: grid;
  grid-template-columns: repeat(7, var(--square-side-length));
  grid-template-rows: repeat(7, var(--square-side-length));
  gap: 0px 0px;
}
